import each from 'lodash/each';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import set from 'lodash/set';
import type { JSONSchemaType } from 'ajv';
import type {
    Preset,
    DashboardJSON,
    EventHandlerDefinition,
    RootDataSourcesDefinition,
    RootVisualizationsDefinition,
    RootInputsDefinition,
    LayoutDefinition,
    DashboardDefaultsDefinition,
} from '@splunk/dashboard-types';

const keyPattern = '^[a-zA-Z0-9_-]*$';

type NullableSchema<T> = JSONSchemaType<T> & {
    nullable: true;
};

const eventHandlerSchema: NullableSchema<EventHandlerDefinition[]> = {
    type: 'array',
    items: {
        type: 'object',
        properties: {
            type: {
                type: 'string',
            },
            options: {
                type: 'object',
                nullable: true,
            },
        },
        required: ['type'],
        additionalProperties: false,
    },
    nullable: true,
};

const dataSourcesSchema: NullableSchema<RootDataSourcesDefinition> = {
    type: 'object',
    nullable: true,
    required: [],
    additionalProperties: false,
    patternProperties: {
        [keyPattern]: {
            type: 'object',
            properties: {
                type: {
                    type: 'string',
                },
                options: {
                    type: 'object',
                    nullable: true,
                },
                // datasource name
                name: {
                    type: 'string',
                    nullable: true,
                },
                // some metadata for this data source
                meta: {
                    type: 'object',
                },
                extend: {
                    type: 'string',
                },
                eventHandlers: eventHandlerSchema,
            },
            required: ['type'],
            additionalProperties: false,
        },
    },
};

